'use client'

import { useState, useEffect } from 'react'

interface FormData {
  name: string
  email: string
  subject: string
  service: string
  message: string
  newsletter: boolean
}

interface FormErrors {
  name?: string
  email?: string
  subject?: string
  message?: string
  general?: string
}

export default function ContactPage() {
  const [formData, setFormData] = useState<FormData>({
    name: '',
    email: '',
    subject: '',
    service: '',
    message: '',
    newsletter: false
  })
  
  const [errors, setErrors] = useState<FormErrors>({})
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [submitSuccess, setSubmitSuccess] = useState(false)

  useEffect(() => {
    // Initialize AOS if available
    if (typeof window !== 'undefined' && (window as any).AOS) {
      (window as any).AOS.refresh()
    }
  }, [])

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const { name, value, type } = e.target
    const checked = (e.target as HTMLInputElement).checked
    
    setFormData(prev => ({
      ...prev,
      [name]: type === 'checkbox' ? checked : value
    }))
    
    // Clear error when user starts typing
    if (errors[name as keyof FormErrors]) {
      setErrors(prev => ({
        ...prev,
        [name]: undefined
      }))
    }
  }

  const validateForm = (): boolean => {
    const newErrors: FormErrors = {}
    
    // Validate name
    if (!formData.name || formData.name.trim().length < 2) {
      newErrors.name = 'Please enter a valid name (at least 2 characters)'
    }
    
    // Validate email
    if (!formData.email || !isValidEmail(formData.email)) {
      newErrors.email = 'Please enter a valid email address'
    }
    
    // Validate subject
    if (!formData.subject || formData.subject.trim().length < 3) {
      newErrors.subject = 'Please enter a subject (at least 3 characters)'
    }
    
    // Validate message
    if (!formData.message || formData.message.trim().length < 10) {
      newErrors.message = 'Please enter a message (at least 10 characters)'
    }
    
    setErrors(newErrors)
    return Object.keys(newErrors).length === 0
  }

  const isValidEmail = (email: string): boolean => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
    return emailRegex.test(email)
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    try {
      setIsSubmitting(true)
      setErrors({})
      
      if (!validateForm()) {
        setIsSubmitting(false)
        return
      }
      
      // Simulate form submission
      await new Promise(resolve => setTimeout(resolve, 1000))
      
      setSubmitSuccess(true)
      setFormData({
        name: '',
        email: '',
        subject: '',
        service: '',
        message: '',
        newsletter: false
      })
      
      // Hide success message after 5 seconds
      setTimeout(() => {
        setSubmitSuccess(false)
      }, 5000)
      
    } catch (error) {
      console.error('Form submission error:', error)
      setErrors({ general: 'An error occurred. Please try again.' })
    } finally {
      setIsSubmitting(false)
    }
  }

  return (
    <>
      {/* Page Header */}
      <section className="bg-gradient-to-r from-blue-600 to-purple-600 text-white py-32 mt-20">
        <div className="max-w-6xl mx-auto px-5 text-center">
          <h1 className="text-5xl md:text-6xl font-bold mb-4" data-aos="fade-up">
            Contact Us
          </h1>
          <p className="text-xl opacity-90 max-w-3xl mx-auto" data-aos="fade-up" data-aos-delay="200">
            Ready to transform your business? Get in touch with our team of experts today.
          </p>
        </div>
      </section>

      {/* Contact Section */}
      <section className="py-20 bg-white">
        <div className="max-w-6xl mx-auto px-5">
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-12">
            {/* Contact Form */}
            <div data-aos="fade-right">
              <h2 className="text-3xl font-bold text-gray-900 mb-4">Send us a Message</h2>
              <p className="text-gray-600 mb-8">
                Fill out the form below and we'll get back to you within 24 hours.
              </p>
              
              {submitSuccess && (
                <div className="bg-green-100 border border-green-400 text-green-700 px-4 py-3 rounded-lg mb-6">
                  Thank you for your message! We will get back to you soon.
                </div>
              )}
              
              {errors.general && (
                <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded-lg mb-6">
                  {errors.general}
                </div>
              )}
              
              <form onSubmit={handleSubmit} className="space-y-6">
                <div>
                  <label htmlFor="name" className="block text-gray-700 font-medium mb-2">
                    Full Name *
                  </label>
                  <input
                    type="text"
                    id="name"
                    name="name"
                    value={formData.name}
                    onChange={handleInputChange}
                    className={`w-full px-4 py-3 border-2 rounded-lg focus:outline-none focus:border-blue-600 transition-colors ${
                      errors.name ? 'border-red-500' : 'border-gray-300'
                    }`}
                    placeholder="Enter your full name"
                  />
                  {errors.name && (
                    <p className="text-red-500 text-sm mt-1">{errors.name}</p>
                  )}
                </div>
                
                <div>
                  <label htmlFor="email" className="block text-gray-700 font-medium mb-2">
                    Email Address *
                  </label>
                  <input
                    type="email"
                    id="email"
                    name="email"
                    value={formData.email}
                    onChange={handleInputChange}
                    className={`w-full px-4 py-3 border-2 rounded-lg focus:outline-none focus:border-blue-600 transition-colors ${
                      errors.email ? 'border-red-500' : 'border-gray-300'
                    }`}
                    placeholder="Enter your email address"
                  />
                  {errors.email && (
                    <p className="text-red-500 text-sm mt-1">{errors.email}</p>
                  )}
                </div>
                
                <div>
                  <label htmlFor="subject" className="block text-gray-700 font-medium mb-2">
                    Subject *
                  </label>
                  <input
                    type="text"
                    id="subject"
                    name="subject"
                    value={formData.subject}
                    onChange={handleInputChange}
                    className={`w-full px-4 py-3 border-2 rounded-lg focus:outline-none focus:border-blue-600 transition-colors ${
                      errors.subject ? 'border-red-500' : 'border-gray-300'
                    }`}
                    placeholder="What's this about?"
                  />
                  {errors.subject && (
                    <p className="text-red-500 text-sm mt-1">{errors.subject}</p>
                  )}
                </div>
                
                <div>
                  <label htmlFor="service" className="block text-gray-700 font-medium mb-2">
                    Service Interest
                  </label>
                  <select
                    id="service"
                    name="service"
                    value={formData.service}
                    onChange={handleInputChange}
                    className="w-full px-4 py-3 border-2 border-gray-300 rounded-lg focus:outline-none focus:border-blue-600 transition-colors"
                  >
                    <option value="">Select a service (optional)</option>
                    <option value="software-engineering">Software Product Engineering</option>
                    <option value="automation">Intelligent Automation</option>
                    <option value="security">IT Security Solutions</option>
                    <option value="cloud">Cloud Services</option>
                    <option value="consultation">General Consultation</option>
                  </select>
                </div>
                
                <div>
                  <label htmlFor="message" className="block text-gray-700 font-medium mb-2">
                    Message *
                  </label>
                  <textarea
                    id="message"
                    name="message"
                    value={formData.message}
                    onChange={handleInputChange}
                    rows={5}
                    className={`w-full px-4 py-3 border-2 rounded-lg focus:outline-none focus:border-blue-600 transition-colors resize-vertical ${
                      errors.message ? 'border-red-500' : 'border-gray-300'
                    }`}
                    placeholder="Tell us about your project or requirements..."
                  />
                  {errors.message && (
                    <p className="text-red-500 text-sm mt-1">{errors.message}</p>
                  )}
                </div>
                
                <div className="flex items-center">
                  <input
                    type="checkbox"
                    id="newsletter"
                    name="newsletter"
                    checked={formData.newsletter}
                    onChange={handleInputChange}
                    className="mr-3 h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
                  />
                  <label htmlFor="newsletter" className="text-gray-700">
                    Subscribe to our newsletter for updates and insights
                  </label>
                </div>
                
                <button
                  type="submit"
                  disabled={isSubmitting}
                  className="w-full bg-gradient-to-r from-blue-600 to-purple-600 text-white font-semibold py-4 px-6 rounded-lg hover:shadow-lg hover:scale-105 transition-all duration-300 disabled:opacity-50 disabled:cursor-not-allowed disabled:hover:scale-100"
                >
                  {isSubmitting ? 'Sending...' : 'Send Message'}
                </button>
              </form>
            </div>
            
            {/* Contact Information */}
            <div data-aos="fade-left">
              <h2 className="text-3xl font-bold text-gray-900 mb-4">Get in Touch</h2>
              <p className="text-gray-600 mb-8">
                We're here to help you succeed. Reach out to us through any of the following channels.
              </p>
              
              <div className="space-y-6 mb-8">
                <ContactItem
                  icon="ðŸ“§"
                  title="Email"
                  content="hello@nextgendigilab.com"
                  description="Send us an email anytime"
                />
                <ContactItem
                  icon="ðŸ“ž"
                  title="Phone"
                  content="+1 (555) 123-4567"
                  description="Call us during business hours"
                />
                <ContactItem
                  icon="ðŸ“"
                  title="Address"
                  content="123 Innovation Drive, Tech City, TC 12345"
                  description="Visit our office"
                />
                <ContactItem
                  icon="ðŸ•’"
                  title="Business Hours"
                  content="Mon - Fri: 9:00 AM - 6:00 PM"
                  description="We're available to help"
                />
              </div>
              
              <div>
                <h4 className="text-xl font-bold text-gray-900 mb-4">Follow Us</h4>
                <div className="flex flex-wrap gap-3">
                  <SocialLink href="#" name="LinkedIn" />
                  <SocialLink href="#" name="Twitter" />
                  <SocialLink href="#" name="Instagram" />
                  <SocialLink href="#" name="GitHub" />
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* FAQ Section */}
      <section className="py-20 bg-gray-50">
        <div className="max-w-4xl mx-auto px-5">
          <div className="text-center mb-16" data-aos="fade-up">
            <h2 className="text-4xl font-bold text-gray-900 mb-4">Frequently Asked Questions</h2>
            <p className="text-xl text-gray-600">Quick answers to common questions</p>
          </div>
          
          <div className="space-y-4">
            <FAQItem
              question="How long does a typical project take?"
              answer="Project timelines vary based on complexity and scope. Simple projects may take 2-4 weeks, while complex enterprise solutions can take 3-6 months. We provide detailed timelines during our initial consultation."
              delay="100"
            />
            <FAQItem
              question="Do you provide ongoing support after project completion?"
              answer="Yes, we offer comprehensive support and maintenance packages. This includes bug fixes, updates, performance monitoring, and feature enhancements based on your needs."
              delay="200"
            />
            <FAQItem
              question="What technologies do you specialize in?"
              answer="We work with modern technologies including React, Node.js, Python, AWS, Docker, and many others. Our team stays current with the latest industry trends and best practices."
              delay="300"
            />
            <FAQItem
              question="Can you work with our existing systems?"
              answer="Absolutely! We specialize in integrating with existing systems and can help modernize legacy applications while maintaining business continuity."
              delay="400"
            />
          </div>
        </div>
      </section>
    </>
  )
}

interface ContactItemProps {
  icon: string
  title: string
  content: string
  description: string
}

function ContactItem({ icon, title, content, description }: ContactItemProps) {
  return (
    <div className="flex items-start space-x-4">
      <div className="text-2xl">{icon}</div>
      <div>
        <h4 className="text-lg font-bold text-gray-900 mb-1">{title}</h4>
        <p className="text-gray-900 font-medium mb-1">{content}</p>
        <p className="text-gray-600 text-sm">{description}</p>
      </div>
    </div>
  )
}

interface SocialLinkProps {
  href: string
  name: string
}

function SocialLink({ href, name }: SocialLinkProps) {
  return (
    <a
      href={href}
      className="text-gray-600 hover:text-blue-600 px-4 py-2 border border-gray-300 rounded-full text-sm font-medium transition-all duration-300 hover:border-blue-600"
    >
      {name}
    </a>
  )
}

interface FAQItemProps {
  question: string
  answer: string
  delay: string
}

function FAQItem({ question, answer, delay }: FAQItemProps) {
  const [isOpen, setIsOpen] = useState(false)
  
  return (
    <div 
      className="border border-gray-200 rounded-lg overflow-hidden"
      data-aos="fade-up" 
      data-aos-delay={delay}
    >
      <button
        className="w-full px-6 py-4 bg-gray-50 hover:bg-gray-100 text-left flex justify-between items-center transition-colors"
        onClick={() => setIsOpen(!isOpen)}
      >
        <h4 className="text-lg font-semibold text-gray-900">{question}</h4>
        <span className="text-2xl text-blue-600 font-bold">
          {isOpen ? 'âˆ’' : '+'}
        </span>
      </button>
      {isOpen && (
        <div className="px-6 py-4 bg-white">
          <p className="text-gray-600 leading-relaxed">{answer}</p>
        </div>
      )}
    </div>
  )
}
